.variantContainer {
    margin-top: 55px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.variantImage {
    height: 130px;
    border: 2px solid var(--variant-border);
    border-radius: 10px;
    margin-right: 20px;
}

.variantDetails {
    width: 100%;
    display: grid;
    grid-row-gap: 50px;
}

.variantTitle {
    font-size: 13px;
    font-weight: 700;
    text-align: left;
    margin-bottom: 0;
    padding-top: 0;
}

.variantKeySpecs {
    font-size: 13px;
    font-weight: normal;
    text-align: left;
    margin-bottom: 0;
    padding-top: 0;
}

.btnContainer {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 20px;
}

.btn {
    margin: 0 auto;
    font-size: 0.8em;
    background-color: transparent;
    border: 1px solid var(--light-grey);
    border-radius: 5px;
    padding: 8px;
    margin-bottom: 10px;
    width: 100%;
    text-transform: uppercase;
    font-weight: 600;
    color: var(--button-text);
    text-align: center;
    text-decoration: none;
  }
  
  .btn:hover {
    background-color: var(--button-hover);
    text-decoration: none;
    color: var(--button-text);
  }

  @media only screen and (max-width: 600px) {
    .btnContainer {
      grid-template-columns: 1fr;
    }
  }