.sidebar {
  position: fixed;
  top: 0px;
  right: 0px;
  width: 120px;

  img {
    cursor: pointer;
    margin-left: auto;
    display: block;
    width: 20px;
  }
}

.menu {
  display: flex;
  align-items: center;
}

.toggleContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  background-color: var(--btn-background);
  padding: 10px;
  cursor: pointer;

  span {
    font-family: var(--modal-font);
    color: white;
    text-shadow: 1.5px 1.5px var(--dark-grey);
    font-size: 0.7rem;
    font-weight: 600;
    letter-spacing: 0.2rem;
    margin-right: 10px;
    margin-left: 10px;
  }
}

.showMenu {
  transform: rotate(0deg);
  transition-duration: 0.5s;
}

.hideMenu {
  transform: rotate(45deg);
  transition-duration: 0.5s;
}

.btnContainerHide {
  opacity: 0;
  transition: all 0.5s ease-out;
}

.btnContainerShow {
  opacity: 1;
  transition: all 0.5s ease-in;
}

.openSidebar {
  position: fixed;
  top: 56px;
  right: 0px;
  z-index: 50;
  border-bottom-left-radius: 5px;
  transition: 1s;
  display: flex;
  flex-direction: column;
  width: 120px;
  height: 100vh;
}

.closeSidebar {
  position: fixed;
  top: 56px;
  right: -200px;
  z-index: 50;
  transition: 1s;
  display: flex;
  flex-direction: column;
  width: 200px;
  height: 100vh;
}

@media (max-width: 926px) and (max-height: 428px) and (orientation: landscape) {
  .sidebar {
    width: 100px;

    img {
      width: 15px;
    }
  }

  .openSidebar,
  .closeSidebar {
    top: 45px;
  }

  .toggleContainer {
    width: 100px;
  }

  .openSidebar {
    width: 100px;
  }
}

@media (max-width: 926px) and (max-height: 428px) and (orientation: landscape) {
  .toggleContainer {
    span {
      font-size: 0.5rem;
      text-shadow: 1px 1px var(--dark-grey);
    }
  }
}

@media only screen and (device-width: 375px) and (device-height: 812px) and (-webkit-device-pixel-ratio: 3) and (orientation: landscape) {
  .openSidebar,
  .closeSidebar {
    top: 45px;
  }
}

@media only screen and (device-width: 414px) and (device-height: 896px) and (-webkit-device-pixel-ratio: 2) and (orientation: landscape) {
  .openSidebar,
  .closeSidebar {
    top: 45px;
  }
}
