.container {
  display: flex;
  font-family: var(--primary-font);
  // justify-content: space-between;
  width: 100%;
  height: 750px;

  // @media (max-width: 926px) and (max-height: 428px) and (orientation: portrait) {
  //   overflow-y: scroll;
  //   flex-direction: column !important;
  // }

  @media only screen and (max-width: 600px) {
    flex-direction: column;
    height: auto;
    // overflow-y: scroll;
  }
}

.mainContent {
  width: 60%;
  display: flex;
  flex-direction: column;
  padding-top: 50px;
  padding-bottom: 50px;
  align-items: center;

  @media only screen and (max-width: 600px) {
    width: 100%;
    max-height: 500px;
  }
}

.productDetailsColumn {
  font-family: var(--modal-font);
  font-weight: 400;
  border-radius: 0px 0px 10px 10px;
  width: 40%;
  padding: 10px 20px;
  display: flex;
  flex-direction: column;

  @media only screen and (max-width: 600px) {
    width: 100%;
    padding: 0;
    margin-top: 150px;
  }
}

.productDetailsColumn::-webkit-scrollbar {
  display: none;
}

.productContent {
  // display: flex;
  // flex-direction: column;
  border-left: 2px solid lightgray;
  flex-grow: 1;
  overflow-y: hidden;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 35px;

  @media only screen and (max-width: 600px) {
    border: none;
    padding: 0;
  }
}

.recProductHeading {
  color: var(--rec-text-heading);
  margin-bottom: 0;
  padding-bottom: 0;
}

.titleAndKeySpecsContainer {
  padding-top: 25px;
  // flex-grow: 1;
  // overflow-y: auto;
}

.titleAndKeySpecsContainer::-webkit-scrollbar {
  display: none;
}

.productContainer {
  overflow: auto;
  scrollbar-width: none;
}

.variantDropdown {
  border: 1px solid var(--dark-grey);

  .variantTitle {
    margin-bottom: 10px;
    font-size: 1em;
  }
}

.indicationAndAR {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em;

  img {
    cursor: pointer;
    max-height: 100%;
    max-width: 95px;
    display: block;
    margin: 0 auto;
  }

  table {
    width: 100%;
    line-height: 1;

    th {
      font-weight: normal;
    }

    td {
      display: inline-block;
    }

    td:nth-child(1) {
      width: 15px;
      text-align: center;
    }

    td:nth-child(2) {
      width: 15px;
      text-align: center;
    }
  }

  @media (max-width: 926px) and (max-height: 428px) and (orientation: landscape) {
    padding: 0.3em 0;
    font-size: 11px;

    img {
      max-height: 40px;
      margin-bottom: 6px;
    }
  }
}

.btnContainer {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  row-gap: 0;
  width: 70%;
  justify-content: space-between;
  margin-top: 20px;
  font-family: var(--primary-font);
  color: #000000;

  @media only screen and (max-width: 600px) {
    width: 100%;
    grid-template-columns: 1fr 1fr;
  }
}

.close {
  border: none;
  background-color: transparent;
  position: absolute;
  top: 6px;
  right: 6px;
  padding: 0;
}

.closeBtn {
  width: 20px;
  margin: 0;
}

.closeBtn:hover {
  color: black;
}
