.excerpt {
  font-size: 1em;
  // margin-top: 1em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em;
  border-bottom: 2px solid var(--dark-grey);

  .excerptLeft {
    display: flex;
    align-items: center;
    width: 100%;
  }

  .close {
    border: none;
    background-color: transparent;
    position: absolute;
    top: 6px;
    right: 6px;
    padding: 0;
  }

  .closeBtn {
    width: 35px;
    margin: 0;
  }

  .closeBtn:hover {
    color: black;
  }

  img {
    max-width: 55px;
    margin-right: 6px;
  }

  p {
    font-size: 1em;
    margin: 0;
    padding: 0.7em;
  }

  @media (max-width: 926px) and (max-height: 428px) and (orientation: landscape) {
    margin-top: 0;
    padding: 0.7em 0;
    border-width: 1px;
    font-size: 10px;

    .closeBtn {
      width: 20px;
    }

    img {
      max-width: 35px;
    }

    p {
      font-size: 0.8em;
      padding: 0;
    }
  }
}