.switchContainer {
    width: 90px;
    height: 36px;
    border-radius: 10px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-weight: 800;
    font-size: 0.7em;
    padding-bottom: 20px;
}

#twoDButton {
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    width: 45px;
    cursor: pointer;
    text-align: center;
    padding: 10px;
}

#threeDButton {
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    width: 45px;
    cursor: pointer;
    text-align: center;
    padding: 10px;
}

.btnActive {
    box-shadow: rgba(99, 99, 99, 0.3) 0px 0px 10px 0px;;
    cursor: pointer;
}

.btnInactive {
    background-color: var(--button-hover);
    color: var(--light-grey);
    cursor: pointer;
}