.contactBtn {
  display: block;
  width: 100%;
  background-color: var(--btn-background);
  color: white;
  font-family: var(--modal-font);
  font-size: 0.7em;
  font-weight: 700;
  letter-spacing: 0.2rem;
  border: 1px solid gray;
  padding: 10px 10px;
  text-align: center;

  @media (max-width: 926px) and (max-height: 428px) and (orientation: landscape) {
    font-size: 0.5rem;
  }
}

.contactBtn:hover {
  text-decoration: none;
  color: white;
}